
import Tab from '../../components/public/navbar'
import axios from 'axios'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "me",
directives: {
TransferDom
},
components:{Tab,XButton,Alert,TransferDom},
data(){
return{
openSimple: false,
show:false,
img_url:"",
user_name:"",
c:""
}
},
created(){
if(this.$store.state.isLogin) {
this.getData();
this.getCode();
}
},
methods:{
openSimpleDialog () {
this.openSimple = true;
},
closeSimpleDialog () {
this.openSimple = false;
},
getData(){
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getCustomerInfo&loginType=2").then((res)=>{
this.user_name=res.data.data.customerName;
})
},

getCode(){
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getBarCode&type=2&loginType=2").then((res)=>{
this.img_url=res
})
},
refreshImg(){
this.$refs.qrcode.src="";
this.$refs.qrcode.src="http://192.168.0.55:8999/customerLoginController.do?method=getBarCode&type=2&loginType=2"
},
mypaper(){
this.$router.push({
path: `/gitpaper`
})
},
myGame(){
this.$router.push({
path: `/gamelist`
})
},
exchange(){
this.$router.push({
path: `/exchangepaper`
})
}
},
beforeDestroy(){
this.$store.commit("updateIsLogin",false);
},
}



