
import comment from '../../components/public/comment'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import scroll from '../scroll/scroll'
import Loading from '../../components/loading/loading'
import axios from 'axios'
export default {
name: "shop_detail",
components:{comment,Swiper,SwiperItem,scroll,Loading},
data() {
return {
user_id:"",
curIdx: 0,
tabs: [],
dataList:[],
imgUrls: [
'http://gw.alicdn.com/imgextra/i4/164/TB2RhntAFGWBuNjy0FbXXb4sXXa_!!164-0-luban.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg'
],
indexImg: 'http://gw3.alicdn.com/bao/uploaded/i4/1798920415/TB21J8JpAKWBuNjy1zjXXcOypXa_!!1798920415-0-item_pic.jpg_210x210.jpg',
}
},
created(){
this.user_id=this.$route.params.id;
this.getData();
},
methods: {
getData(){
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getMerchantInfoById&merchantId="+this.user_id+"&loginType=2").then((res)=>{
this.dataList=res.data.data;
})
},
call(){
window.location.href = 'tel://18637280068'
},
selectItem(){
this.$router.push({
path: `/picture`
})
},

back() {
this.$router.go(-1);
this.$destroy();
},
},
}



