
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
created(){
this.getData()
},

methods:{
getData(){
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getMyCoupon&page=1&rows=5&loginType=2").then((res)=>{
this.allPaper=res.data.dataList;
})
},

back() {
this.$router.go(-1);
},
}
}



