import {debounce} from "../../common/js/util";

export default {
  name: "search-box",
  props: {
    placeholder: {
      type: String,
      default: '搜索商家名字'
    }
  },
  data() {
    return {
      query: ""
    }
  },
  methods: {
    //清除搜索内容
    clear() {
      this.query = ""
    },
    //使输入框失去焦点
    blur(){
      this.$refs.query.blur()
    }
  },
  created() {
    //监听搜索内容
    this.$watch('query', debounce((newQuery) => {
      //向外派发搜索事件
      this.$emit('query', newQuery)
    },200))
  }
}



