import axios from 'axios'
export default {
  name: "gift-paper",
  data(){
    return{
      id:"",
      dataList:[]
    }
  },
  created(){
    this.id=this.$route.params.id;
    this.getData(this.id)
  },

  methods:{

    getData(id){
      axios.get("http://192.168.0.55:8999/customerLoginController.do?method=getMerchantsByAcId&activityId=402880b764efac030164efb41727000e&page=1&rows=10&loginType=2").then((res)=>{
        console.log(res.data)
        this.dataList=res.data.dataList
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/shop/${item.merchantId}`
      })
    },
    //返回上一级页面
    back() {
      this.$router.go(-1);
      this.$destroy();
    },
  }
}



