
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
export default {
name: "login",
components:{XInput,Group,XButton},
data(){
return{
value:'',
code:""
}
},
methods:{
getCode(){

},
login(){
this.$router.replace({
path: '/home',query:{}
});
this.$store.commit('updateIsLogin', true)
axios.get("http://192.168.0.55:8999/customerLoginController.do?method=login&phone="+this.value+"&type=1&code=9759&loginType=2").then((res)=>{
console.log(res.data)
if(res.data.ret==="200"){
this.$router.replace({
path: '/home',query:{}
});
this.$store.commit('updateIsLogin', true)
}else {
alert("手机号不存在")
}
})
}
}
}



