
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: "game",
components:{Alert,TransferDom},
data() {
return {
show:false,
flag: false,
}
},
created() {
console.log(location.href.split('#')[0]);
},
mounted() {
this.play();
this.InitWx();
},
methods: {
InitWx(){
$.post('http://weixin20.shushangsoft.com/JsapiTicket/GetJsapiTicket/',
{ officialAccountId: 'gh_36d980f25e97', url: location.href.split('#')[0], jsApiLists: 'scanQRCode', debug: false }, function (config) {
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: config.appId,
timestamp: config.timestamp,
nonceStr: config.nonceStr,
signature: config.signature,
jsApiList: config.jsApiList
});

})

















wx.error(function (res) {
alert("出错了：" + res.errMsg);
});
},
clickCode(index) {
wx.ready(function () {
wx.checkJsApi({
jsApiList: ['scanQRCode'],
success: function (res) {
}
});
wx.scanQRCode({
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {
let result = res.resultStr; 
alert(result);
const boat=$('.boat')
if(index===4){
boat.css({"bottom":" 68%","left":"52%"})
}
else if(index===3){
boat.css({"bottom":" 55%","left":"29%"})
}
else if(index===2){
boat.css({"bottom":" 38%","left":"32%"})
}
else if(index===1){
boat.css({"bottom":" 15%","left":"70%"})
}
else if(index===0){
boat.css({"bottom":" 10%","left":"50%"})
}
}
});

});
},
play() {
this.flag = true;
const audio = document.getElementById('music');
audio.play();
},

back() {
this.$router.go(-1);
this.$destroy()
},
setClass(index) {
if (index === 0) {
return "brand-position0"
}
else if (index === 1) {
return "brand-position1"
}
else if (index === 2) {
return "brand-position3"
}
else if (index === 3) {
return "brand-position4"
}
else if (index === 4) {
return "brand-position6"
}
}
}
}



